/* Section 00  */
.section00 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  gap: 30px;
  position: relative;
  width: 100%;
  max-width: 540px;
  margin: 0 auto;
  background: rgba(136, 211, 188, 0.15);
  border-radius: 0; /*  */
}

/*  */
.titleWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  gap: 15px;
  width: 100%;
  max-width: 500px;
}

.logoContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 98px;
  height: 33px;
  margin-bottom: -10px;
}

.roadText {
  font-family: var(--font-primary);
  font-size: 16.5px;
  font-weight: bold;
  color: var(--color-black);
  margin-right: 3px;
  display: flex;
  align-items: center;
  position: relative;
  top: 10px; /* 10px */
}

.logoChar {
  width: auto;
  height: 66px;
  max-height: 66px;
  margin: 0 2px;
  object-fit: contain;
  position: relative;
  top: 5px;
}

/* R, O, A, D */
.logoCharSmall {
  width: auto;
  height: 16.5px;
  max-height: 16.5px;
  margin: 0 1px;
  object-fit: contain;
}

/*  */
.subtitle {
  font-family: 'Zen Kaku Gothic New', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #333;
  text-align: center;
  margin: 0;
}

.mainTitle {
  width: 100%;
  max-width: 394px;
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 1.3;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  color: var(--color-black);
  margin: -5px 0;
}

.dotline {
  width: 100%;
  max-width: 364px;
  height: 10px;
  background-size: contain;
  background-repeat: repeat-x;
}

/*  */
/* .contentFrame - ContentFrame.module.css */

.projectButton {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px 10px;
  gap: 10px;
  width: 100%;
  max-width: 280px;
  height: 46px;
  background: #FDF9D5;
  border: 1px solid var(--color-black);
  border-radius: 5px;
  cursor: pointer;
}

.bookIcon {
  width: 30px;
  height: 30px;
  background-image: url('../../../assets/images/note.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.projectButton span {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: var(--color-black);
  flex-grow: 1;
  text-align: center;
}

/*  */
.experienceSection {
  width: 100%;
  max-width: 500px;
  margin-top: 20px;
}

.experienceTitle {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 32px;
  color: var(--color-black);
  margin-bottom: 15px;
}

.tweetArea {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap; /* wrapnowrap */
  gap: 20px;
  width: 100%;
  justify-content: flex-start; /*  */
  overflow-x: auto; /*  */
  margin-bottom: 20px;
  -webkit-overflow-scrolling: touch; /*  */
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IEEdge */
}

/* Chrome, Safari, Opera */
.tweetArea::-webkit-scrollbar {
  display: none;
}

/*  TweetCard.module.css  */

.moreButton {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px 16px;
  gap: 10px;
  width: auto; /*  */
  min-width: 236px; /*  */
  height: 40px;
  background: var(--color-white);
  border: 1px solid var(--color-black);
  border-radius: 5px;
  margin: 20px auto 0;
  cursor: pointer;
}

.playIcon {
  width: 19.2px; /* 24px  0.8 = 19.2px */
  height: 19.2px; /* 24px  0.8 = 19.2px */
  object-fit: contain;
}

.moreButton span {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: var(--color-black);
  white-space: nowrap; /*  */
}

/* Section00ExperienceSection */
.experience00 :global(.titleLine2) {
  font-size: 18px !important;
}

/*  */
@media (max-width: 768px) {
  .section00 {
    padding: 30px 15px;
  }
  
  .tweetArea {
    flex-direction: row; /*  */
    flex-wrap: nowrap; /*  */
    align-items: stretch; /*  */
    justify-content: flex-start; /*  */
  }
  /*  TweetCard.module.css  */
}
