/*  -  */

/*  */
.navItem {
  width: fit-content; /* 100%  fit-content  */
  min-width: 100%; /* 100% */
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px 0px 0px 30px;
  gap: 5px; /* 10px  5px   */
  transition: background-color 0.2s;
  border-radius: 5px;
  cursor: pointer; /*  */
  overflow: visible;
}

.navItem:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

/*  */
.navItemHeader {
  display: flex;
  align-items: center;
  gap: 8px; /* 12px  8px  */
  margin-bottom: 10px;
  width: 100%;
  height: 20px;
  flex-wrap: nowrap;
  pointer-events: none; /*  */
}

/*  */
.navIcon {
  width: 26px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: transparent;
}

/*  */
.navTitle {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px; /* 28px  20px Figma2 */
  letter-spacing: 0.05em; /*  */
  color: var(--color-black);
  white-space: pre-wrap; /* nowrap  pre-wrap \n */
  overflow: visible; /* hidden  visible  */
  max-width: 100%;
  word-break: keep-all; /*  */
  transition: color 0.2s;
}

/*  */
.navItem:hover .navTitle {
  color: #555;
}

/*  */
.navSubItems {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px 0px 0px 35px; /* 0px 15px 0px 38px  0px 0px 0px 35px  */
  gap: 0px; /* 8px  4px  */
  width: 100%;
  box-sizing: border-box;
  pointer-events: none; /*  */
}

/*  */
.navSubItem {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: var(--color-black);
  margin-bottom: 0px; /* 8px  0px  */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  transition: color 0.2s;
}

/*  */
.navItem:hover .navSubItem {
  color: #555;
}

/*  */
.searchSection {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 5px; /* 10px  0px 0 */
  margin-top: -30px; /* 0px  -30px navigationgap */
}

.searchTitle {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 23px;
  color: var(--color-black);
  padding: 0px 0px 0px 0px;
  text-align: center;
  width: 100%;
}

/*  */
.dividerLine {
  width: 100%;
  height: 0px;
  border: none;
  border-top: 2px dotted #000000; /* dashed  dotted () */
  margin: 0;
}

.searchItems {
  display: flex;
  flex-direction: column;
  gap: 0px; /* 8px  0px 0 */
  width: 100%;
  white-space: nowrap;
}

.searchItem {
  font-family: var(--font-secondary);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 23px;
  color: var(--color-black);
  padding: 0px 0px 0px 0px; /* 30px  0px  */
  cursor: pointer;
  transition: color 0.2s, background-color 0.2s;
  border-radius: 5px;
  white-space: nowrap;
  width: fit-content;
  min-width: 100%;
  overflow: visible;
}

.searchItem:hover {
  color: #555;
  background-color: rgba(0, 0, 0, 0.05);
}

/*  */
.navFooter {
  width: fit-content; /* 100%  fit-content  */
  min-width: 100%; /* 100% */
  padding: 5px 8px;
  font-family: var(--font-secondary);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 23px;
  color: var(--color-black);
  margin-top: -30px; /* 0px  -30px navigationgap */
  transition: color 0.2s, background-color 0.2s;
  border-radius: 5px;
  white-space: nowrap;
  overflow: visible;
}

.navFooter:hover {
  color: #555;
  background-color: rgba(0, 0, 0, 0.05);
}

/*  */
.navHeader {
  width: 100%;
  height: 79px;
  position: relative;
  margin-bottom: 0;
  padding-bottom: 0;
}

/*  */
.navLogo {
  position: absolute;
  width: 151px;
  height: 30px;
  left: 6px;
  top: 30px; /* 49px  46px: 14px(Figma) */
  background-image: url('../../assets/images/logo.png');
  background-size: contain;
  background-repeat: no-repeat;
  transition: opacity 0.2s;
}

.navLogo:hover {
  opacity: 0.8;
}

/*  */
.navHeaderTitle {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: var(--color-black);
  position: absolute;
  left: 0;
  top: 0;
  width: 216px;
  height: 9px;
  margin: 0;
  transition: color 0.2s;
}

.navHeaderTitle:hover {
  color: #555;
}

/*  */
.navHeaderSubtitle {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: var(--color-black);
  position: absolute;
  left: 163px;
  top: 46px; /* 32px  61px  */
  margin-left: 0;
  transition: color 0.2s;
}

.navHeaderSubtitle:hover {
  color: #555;
}
