/*  */
.titleSection {
  /*  */
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding: 0px;
  /* gap: 33px; ( % ) */
  isolation: isolate;

  position: relative;
  width: 100%;
  max-width: 540px;
  
  /*    */
  /* height: 513px; () */
  aspect-ratio: 540 / 513; /*  */
  /*    */
  
  margin: 0 auto;
  border-radius: 10px 10px 0px 0px;
  background-color: transparent; /*  */
  overflow: hidden; /*  */
}

/*  */
.mainTitle {
  position: absolute;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  text-align: center;
  color: #000000;
  
  /* z-index */
  z-index: 0;
  
  /*  */
  white-space: nowrap; 

  /*  %  */
  width: 50%; /* (270 / 540) */
  height: 3.87%; /* (19.88 / 513) */
  left: 5.92%; /* (32 / 540) */
  top: 5.84%; /* (30 / 513) */

  /*  (clamp(, , )) */
  font-size: clamp(3.88px, 2.59vw, 14px); 
  line-height: 1.2; 
}

.mainLogo {
  position: absolute;
  background-image: url('../../../assets/icons/logo2.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  
  /* z-index */
  z-index: 2;

  /*  %  */
  width: 50.22%; /* (271.21 / 540) */
  height: 14.42%; /* (74 / 513) */
  left: 7.22%; /* (39 / 540) */
  top: 10.72%; /* (55 / 513) */
}

/*  */
.prefectureLabel {
  position: absolute;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 600;
  text-align: center;
  color: #333333;

  /* z-index */
  z-index: 2;

  /*  %  */
  width: 50.22%; /* (271.21 / 540) */
  height: auto;
  left: 7.22%; /* (39 / 540) */
  top: 22.41%; /* (115 / 513) */
  
  /*  */
  font-size: clamp(4.44px, 2.96vw, 16px);
  line-height: 1.2;
}

/*  */
.rainbowImage {
  position: absolute;

  /* z-index */
  z-index: 3;

  /*  %  */
  width: 122.73%; /* (662.77 / 540) */
  height: 71.54%; /* (367 / 513) */
  left: -11.29%; /* (-61 / 540) */
  top: 19.68%; /* (101 / 513) */
}

.rainbowImage img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* AdobeStock2 (main_img2.png) */
.adobeStockImage {
  position: absolute;

  /* z-index */
  z-index: 4; /* rainbow(3)  */

  /*  %  */
  width: 75.3%; /* (406.62 / 540) */
  height: 75.43%; /* (387 / 513) */
  right: 0; /*  */
  top: 15%; /*  */
}

.adobeStockImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}


