/* 3 */
/* .frame17, .frame18, .frame19  */
.frame17,
.frame18,
.frame19 {
  position: absolute;
  z-index: 5; /* adobeStockImage (z-index: 4)  */
  
  /*  (Figma 476.jpg ) */
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(2px); /*  */
  border-radius: 8px; /*  */
  padding: 5px 10px; /*  */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

  border: 1px solid black; /* 1px */

  /*  () */
  font-family: var(--font-primary);
  font-weight: 500;
  color: var(--color-black);
  text-align: center;
  /*  */
  font-size: clamp(12px, 3.5vw, 15px); 
  line-height: 1.4;
  
  /* margin: 0 */
  margin: 0; 
  
  /* ---    --- */
  /* transform: translateX(-50%)50%() */
  /*  left: 50%  */
  /* transform: translateX(-50%); */ /*   */
  /* ---    --- */
}

/* Frame 17:  */
.frame17 {
  top: 66%;
  left: 3%; 
}

/* Frame 18: ... */
.frame18 {
  top: 75%;
  left: 3%; 
  white-space: nowrap; /* <br>  */
}

/* Frame 19:  */
.frame19 {
  top: 84%;
  left: 3%; 
  white-space: nowrap; 
}