/*  */
.bannerArea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 0px;
  gap: var(--space-xl);
  width: 200px; /*  */
  height: auto;
  min-height: 270px;
  flex: none;
  order: 2;
  flex-grow: 0;
  z-index: 100;
  position: fixed;
  top: 51px;
  right: 100px; /* 1305px */
}

/* 1304px1px100px */
@media (max-width: 1304px) {
  .bannerArea {
    right: calc(100vw - 1204px); /*  */
    /* : 1205px right: 1px */
  }
}

/* 1204px */
@media (max-width: 1204px) {
  .bannerArea {
    display: none;
  }
}
