/* src/components/common/Footer/Footer.module.css */
/* Figma ( 481)  */

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  /*  (50px, 30px) */
  padding: 50px 30px; 
  /*  */
  gap: 30px; 
  width: 100%;
  max-width: 540px;
  /*  */
  height: auto; 
  /*  88D3BC  */
  background: #88D3BC; 
  flex: none;
  order: 3;
  flex-grow: 0;
  margin-top: 0;
  border-radius: 0; 
  box-sizing: border-box; 
}

.footerContent {
  display: flex;
  flex-direction: column;
  /*    */
  align-items: flex-start; 
  /*  */
  gap: 15px; 
  width: 100%;
  /*  */
  max-width: 480px; 
  height: 100%;
  justify-content: center;
}

.footerTitle {
  width: auto; 
  height: auto;
  font-family: 'Zen Kaku Gothic New', sans-serif;
  font-style: normal;
  font-weight: 400; /* Medium or Regular */
  font-size: 12px; /* 12px */
  line-height: 1.5;
  /*    */
  color: #555555; 
  /*    */
  text-align: left; 
  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0;
}

.footerLogo {
  /*    */
  width: 270px; 
  height: auto; 
  flex: none;
  order: 1;
  flex-grow: 0;
  display: flex;
  justify-content: center; /*  */ 
  align-items: center;
  align-self: center; /*  */
  transition: opacity 0.3s ease, transform 0.2s ease;
  border-radius: 4px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  margin-top: -5px; /*  */
}

.footerLogo:hover {
  opacity: 0.8;
  transform: scale(1.05);
}

.footerLogo:focus {
  /*  */
  outline: 2px solid #555555; 
  outline-offset: 2px;
}

.footerLogo:active {
  transform: scale(0.98);
}

.logoImage {
  width: 100%;
  height: 100%;
  object-fit: contain;
  pointer-events: none; 
  user-select: none; 
}

/*   .footerMenu  .menuItem  .footerList   */
.footerList {
  /*  */
  list-style: none; 
  padding: 0;
  margin: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  /*  */
  align-items: flex-start; 
  /*  */
  gap: 2px; 
  flex: none;
  order: 2;
  flex-grow: 0;
  /*  */
  margin-top: 15px; 
}

.footerList li {
  font-family: 'Zen Kaku Gothic New', sans-serif;
  font-style: normal;
  font-weight: 500; /* Medium */
  font-size: 14px;
  line-height: 1.5; /*  */
  /*  */
  color: #555555; 
  cursor: pointer;
  transition: color 0.2s;
}

/*    */
.footerList li:hover {
  color: #000000; 
}

/*  */
.lastRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

/*  */
.submitButton {
  background: white;
  padding: 6px 12px;
  border-radius: 4px;
  font-weight: 600;
  transition: background-color 0.2s, color 0.2s;
  margin-right: 20px;
}

.submitButton:hover {
  background: #f0f0f0;
  color: #000000;
}
/*    */


/*  */
@media (max-width: 768px) {
  .footer {
    padding: 40px 20px;
    height: auto;
    max-width: 100%;
    gap: 20px;
  }
  
  .footerContent {
    gap: 15px; 
    /*  */
    max-width: 480px; 
    width: 100%;
  }
  
  .footerTitle {
    font-size: 12px;
  }
  
  .footerLogo {
    width: 240px; 
  }
  
  .footerList li {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 30px 20px; 
    gap: 15px;
  }
  
  .footerContent {
    gap: 10px;
    max-width: 100%; /*  */
  }
  
  .footerTitle {
    font-size: 11px;
  }
  
  .footerLogo {
    width: 220px;
  }
  
  .footerList li {
    font-size: 13px;
  }
}