/*  */
.container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 51px var(--space-xxl);
  gap: 72px;
  isolation: isolate;
  position: relative;
  width: 100%;
  max-width: 1304px;
  margin: 0 auto;
  background: var(--color-background);
}

/*  */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    padding: 20px;
    gap: 20px;
  }
}

/* 982px: 540px */
@media (max-width: 982px) {
  .container {
    padding: 0;
    gap: 0;
  }

  .mainContent {
    width: 540px;
    min-width: 540px;
    max-width: 540px;
    margin: 60px auto 0; /* 60pxauto */
  }

  .hamburgerMenu {
    display: block;
    position: fixed;
    top: 20px;
    left: 20px;
    z-index: 101;
    background-color: rgba(255, 255, 255, 0.95);
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  }

  /*  */
  .mainTitle {
    left: 50%;
    transform: translateX(-50%);
    width: 80%;
  }
  
  .mainLogo {
    left: 50%;
    transform: translateX(-50%);
    width: 80%;
    max-width: 283.5px;
  }
}

/* 560px: 96.43%1.79% */
@media (max-width: 560px) {
  .mainContent {
    width: 96.43%;
    min-width: unset;
    max-width: 96.43%;
    margin: 60px 1.79%; /* 60px1.79% */
  }
}

/*  */
.navigation {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 32px 23px; /* 32px 30px  32px 23px  */
  gap: 30px; /* 0px  30px  */
  isolation: isolate;
  width: 270px; /*  */
  height: auto; /* calc(100vh - 102px)  auto  */
  max-height: calc(100vh - 51px - 20px); /* top(51px) + (20px) */
  min-height: auto; /* 670px  auto  */
  background: var(--color-white);
  border: 2px solid var(--color-black);
  border-radius: var(--radius-l);
  flex: none;
  order: 0;
  flex-shrink: 0;
  z-index: 1;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE/Edge */
  position: fixed;
  left: 50px; /*  */
  top: 51px;
}

/* 982px */
@media (max-width: 982px) {
  .navigation {
    display: none;
  }
}

/* Chrome, Safari, Opera */
.navigation::-webkit-scrollbar {
  display: none;
}

/*  -  */
.navItemsContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  margin-top: 50px; /* 90px  74px: (44px) + gap(30px) = Figma */
}

/*  - Vector */
.navigation::before {
  content: '';
  position: absolute;
  width: 17px;
  height: calc(100% - 210px); /*  */
  left: 23px; /* -7px30px23px */
  top: 114px; /* 149px  125px margin-top 74px50px24px */
  background-image: url('../assets/images/vector.png');
  background-size: 17px 100%; /* 94% */
  background-repeat: repeat-y; /*  */
  background-position: top;
  z-index: 8;
}

/*  */
.hamburgerMenu {
  display: none;
  cursor: pointer;
}

.menuIcon {
  width: 30px;
  height: 3px;
  background-color: #000;
  margin: 6px 0;
  border-radius: 2px;
}

/*  */
.mainContent {
  width: var(--content-width);
  min-width: 390px;
  max-width: var(--content-width);
  height: auto;
  background: var(--color-white);
  border-radius: var(--radius-m);
  flex: none;
  order: 1;
  flex-grow: 0;
  z-index: 1;
  margin: 0 auto;
  position: relative;
}

/*  - Figma */
.girlImage {
  position: absolute;
  width: 136px;
  height: 268px;
  /*  - 100px100px */
  right: 77px;
  top: 353px;
  background-image: url('../assets/images/girl1.png');
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 3;
}

/*  */
.opening {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 30px;
  gap: var(--space-xl);
  position: relative;
  width: 100%;
  max-width: var(--content-width);
  height: auto;
}

.openingText {
  width: 100%;
  max-width: 480px;
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 500;
  font-size: var(--font-s);
  line-height: var(--line-height-xl);
  letter-spacing: 0.1em;
  color: var(--color-black);
  /* leading-trim: both; */
  /* text-edge: cap; */
}

.projectButton {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px var(--space-m);
  gap: 10px;
  width: 180px;
  height: 40px;
  background: var(--color-button-bg);
  border: 1px solid var(--color-black);
  border-radius: var(--radius-s);
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: var(--font-xs);
  line-height: var(--line-height-s);
  color: var(--color-black);
  cursor: pointer;
  /* leading-trim: both; */
  /* text-edge: cap; */
}

/*  */
.titleSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px;
  gap: 33px;
  position: relative;
  width: 100%;
  max-width: var(--content-width);
  height: 350px;
  border-radius: var(--radius-m) var(--radius-m) 0px 0px;
  background-image: url('../assets/images/AdobeStock.png');
  /* background-color: red;  */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

/*  */
.mainTitle {
  position: absolute;
  width: 270px;
  height: 19.88px;
  left: 135px;
  top: 73px;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #FFFFFF;
}

.mainLogo {
  position: absolute;
  width: 283.5px;
  height: 42px;
  left: 142px;
  top: 122px;
  background-image: url('../assets/images/logo1.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

/* 982px */