/*  */
.container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 51px 0;
  gap: 72px; /*  */
  isolation: isolate;
  position: relative;
  width: 100%;
  max-width: 1304px;
  margin: 0 auto; /* 1304px */
  background: var(--color-background);
}

/*  */
.mobileSpacer {
  height: 80px; /*  +  */
  width: 100%;
  background: transparent;
}

/* 982px */
@media (max-width: 982px) {
  .container {
    flex-direction: column;
    padding: 0;
    gap: 0;
    align-items: center;
  }
}