/* .mainContent  @media  commonPageLayout.module.css 
  
  (max-width: 982px)  (max-width: 560px)  .mainContent 
*/
/* ---   --- */


/*  () */
.headerSection {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0px; /*  */
}

/* ROAD () */
.roadSections {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0; /* ROAD */
  margin-top: 0px; /*  */
}

/*  () */
@media (max-width: 982px) {
  .headerSection {
    gap: 30px; 
    padding: 0 10px; 
    width: 100%; /* 100% */
  }
  
  .roadSections {
    margin-top: 30px; 
    padding: 0; /*  */
    width: 100%; /* 100% */
  }
}

