/* : playIcon */
.playIcon {
    width: 19.2px;
    height: 19.2px;
    background-image: url('../../../assets/images/vectorRB.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 8px;
}
/* Section05  -  */
.section05 {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 20px 40px;
    /*gap: 30px;*/
    position: relative;
    width: 100%;
    max-width: 540px;
    margin: 0 auto;
    background: #FFFFFF;
    border-radius: 0;
    overflow: visible;
}

/*  - H1 00 */
.titleWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    gap: 15px;
    width: 100%;
    max-width: 500px;
    margin-bottom: 30px; /*   */
}

.logoContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 98px;
  height: 30px;
  margin-bottom: 0px;
}

.roadText {
  font-family: var(--font-primary);
  font-size: 16.5px;
  font-weight: bold;
  color: var(--color-black);
  margin-right: 2px;
  display: flex;
  align-items: center;
  position: relative;
  top: 10px;
}

.logoChar {
  width: auto;
  height: 66px;
  max-height: 66px;
  margin: 0 1px;
  object-fit: contain;
  position: relative;
  top: 5px;
}

.mainTitle {
    width: 100%;
    max-width: 200px;
    font-family: 'DNP ShueiGoKinStd', var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 35px;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    color: #000000;
    margin-top: 0px;
    margin-bottom: 0px;
}

.dotline {
    width: 100%;
    max-width: 364.5px;
    height: 10px;
    background-size: contain;
    background-repeat: repeat-x;
    margin: 0;
}

/*  */
.description {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 100%;
    max-width: 480px;
    padding: 0 10px;
    margin-bottom: 30px;
}

.descriptionTitle1 {
    font-family: var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.5;
    color: var(--color-black);
    margin: 0;
    text-align: center;
    width: 100%;
}

.descriptionTitle2 {
    font-family: var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.5;
    color: var(--color-black);
    margin: 0;
    text-align: center;
    width: 100%;
}

.descriptionText {
    font-family: var(--font-primary);
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.7;
    color: var(--color-black);
    margin: 0;
    text-align: left;
    width: 100%;
}

/*  */
.placeSection {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    gap: 20px;
    width: 100%;
    max-width: 500px;
}

.placeHeader {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
    width: 100%;
}

.placeTag {
    font-family: var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.5;
    color: var(--color-black);
}

.placeTitle {
    font-family: var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 1.4;
    color: var(--color-black);
    margin: 0;
}

.placeCardArea {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 10px 0;
    gap: 15px;
    width: 100%;
    overflow-x: auto;
    overflow-y: hidden;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.placeCardArea::-webkit-scrollbar {
    display: none;
}

.moreButton {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 20px;
    gap: 8px;
    width: auto;
    max-width: 360px;
    height: auto;
    background: rgba(121, 181, 238, 0.15);
    border: 1px solid var(--color-black);
    border-radius: 8px;
    font-family: var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: var(--color-black);
    cursor: pointer;
    transition: background-color 0.2s, transform 0.1s;
}

.moreButton:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

.moreButton:active {
    transform: scale(0.98);
}

.buttonIconContainer {
    width: 24px;
    height: 24px;
    position: relative;
}

.playArrowIcon {
    position: absolute;
    left: 8px;
    top: 6px;
    width: 12px;
    height: 12px;
    fill: #000000;
}

/*  -  */
.introSection {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 24px 20px;
    gap: 30px;
    width: 100%;
    max-width: 500px;
    background: #FFFFFF;
    border-radius: 10px;
    margin-bottom: 30px; /*   */
}

.introTitle {
    width: 192px;
    height: 12px;
    font-family: 'DNP ShueiGoKinStd', var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 32px;
    text-align: center;
    color: #000000;
    margin: 0;
    flex: none;
    order: 0;
    flex-grow: 0;
}

.introText {
    width: 100%;
    max-width: 460px;
    font-family: 'DNP ShueiGoKinStd', var(--font-primary);
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 28px;
    letter-spacing: 0.1em;
    color: #000000;
    margin: 0;
}

/*  -  */
.experienceSection {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 20px 40px;
    gap: 15px;
    width: 100%;
    max-width: 500px;
    background: #FFFFFF;
    border-radius: 10px;
    /*margin-bottom: -50px;*/
    overflow: visible;
    margin-top: 30px; /*  introSection */
    margin-bottom: 30px; /*  expertSection */

}

/* Section05ExperienceSection */
.experience05 :global(.titleLine2) {
  font-size: 18px !important;
}

/*  */
.experienceButton {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 8px 16px;
    width: 320px;
    height: 40px;
    background: #FDF9D5;
    border: 1px solid #000000;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 0px;
}

.experienceButton:hover {
    background: rgba(253, 249, 213, 0.8);
}

.experienceButtonIcon {
    width: 24px;
    height: 24px;
    position: relative;
    margin-right: 8px;
}

.experienceButtonIcon svg {
    position: absolute;
    left: 33.33%;
    right: 24.39%;
    top: 24.25%;
    bottom: 24.24%;
    width: 12px;
    height: 12px;
    fill: #000000;
}

.experienceButtonText {
    font-family: 'DNP ShueiGoKinStd', var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    white-space: nowrap;
}

.expertSection {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    gap: 15px;
    width: 100%;
    max-width: 500px;
    background: #FFFFFF;
    border-radius: 10px;
    margin-bottom: 20px;
    margin-top: 0;
}

.sectionHeader {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 2px 0px;
    gap: 8px;
    width: 100%;
    max-width: 500px;
    height: 39.31px;
}

.sectionTag {
    width: 132px;
    height: 9px;
    font-family: 'DNP ShueiGoKinStd', var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    flex: none;
    order: 0;
    flex-grow: 0;
}

.sectionTitle {
    width: 256px;
    height: 12px;
    font-family: 'DNP ShueiGoKinStd', var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 32px;
    color: #000000;
    margin: 0;
    flex: none;
    order: 1;
    flex-grow: 0;
}

/*  -  */
.tweetCardArea {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 0;
    gap: 20px;
    width: 100%;
    overflow-x: auto;
    overflow-y: visible;
    flex-wrap: nowrap;
    margin-bottom: 20px;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE and Edge */
    -webkit-overflow-scrolling: touch; /*  */
}

/* Chrome, Safari, Opera */
.tweetCardArea::-webkit-scrollbar {
    display: none;
}

/* PicCard -  */
.tweetCardArea > * {
    display: flex;
    width: 240px;
    max-width: 240px;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0; /*  */
    margin-top: 20px;
}

/*  -  */
.projectButton {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 8px 16px;
    min-width: 320px;
    max-width: fit-content;
    height: 40px;
    background: #FDF9D5;
    border: 1px solid #000000;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 0;
    margin-bottom: 20px;
    white-space: nowrap;
}

.buttonIconContainer {
    width: 24px;
    height: 24px;
    position: relative;
    margin-right: 8px;
}

.playArrowIcon {
    position: absolute;
    left: 8px;
    top: 6px;
    width: 12px;
    height: 12px;
    fill: #000000;
}

.projectButton span {
    font-family: 'DNP ShueiGoKinStd', var(--font-primary);
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    white-space: nowrap;
    display: inline-block;
}

.projectButton:hover {
    background: rgba(0, 0, 0, 0.05);
}

/*  */
/* .expertButton, .experienceButton  */

/*  -  */
@media (max-width: 768px) {
    .section05 {
        padding: 40px 16px 30px;
        width: 100%;
        max-width: 100%;
    }

    .titleWrapper {
        width: 100%;
        max-width: 100%;
    }

    .mainTitle {
        font-size: 22px;
        line-height: 28px;
        width: 100%;
        max-width: 300px; /*  */
    }

    .introText {
        font-size: 13px;
        line-height: 24px;
        width: 100%;
    }

    .introSection {
        width: 100%;
        max-width: 100%;
        padding: 24px 16px; /*  */
    }

    .experienceSection {
        width: 100%;
        max-width: 100%;
        padding: 20px 16px 40px; /*  */
    }

    .expertSection {
        width: 100%;
        max-width: 100%;
        padding: 20px 16px; /*  */
    }

    .sectionHeader {
        width: 100%;
        padding-left: 0;
    }

    .tweetCardArea {
        width: 100%;
        padding: 0 4px; /*  */
        /*  */
    }

    .projectButton {
        width: 280px;
        margin: 20px auto;
    }
}

@media (max-width: 480px) {
    .mainTitle {
        font-size: 20px;
        line-height: 26px;
        max-width: 250px; /*  */
    }

    .sectionTitle {
        font-size: 15px;
        width: 100%;
    }

    .sectionTag {
        font-size: 11px;
        width: 100%;
    }

    .projectButton {
        width: 240px;
        font-size: 11px;
    }

    .tweetCardArea {
        width: 100%;
        padding: 0 8px; /*  */
        /*  */
    }
}