/* src/components/MainContent/01/Section01Content.module.css */
/* 01  */

/*  */
.section01Content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* 00 */
.titleSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 30px 20px;
  gap: 5px;
  width: 100%;
  max-width: 540px;
  position: relative;
  isolation: isolate;
  min-height: 150px;
}

.roadImage {
  height: auto;
  max-width: 200px;
  margin-bottom: -10px;
}

.mainTitle {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 1.3;
  text-align: center;
  color: #333;
  margin: -5px 0;
}

.dotline {
  width: 90%;
  max-width: 400px;
  height: auto;
  margin: 10px 0;
}

/*  */
.descriptionSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 30px;
  gap: 15px;
  width: 100%;
}

.descriptionTitle {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.6;
  text-align: left;
  color: var(--color-black);
  margin: 0;
  width: 100%;
}

.dividerLine {
  width: 100%;
  height: 1px;
  background-color: #CCCCCC;
  margin: 10px 0;
}

.descriptionText {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.8;
  text-align: left;
  color: var(--color-black);
  margin: 0;
  width: 100%;
}

/*  */
.interviewSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  padding: 30px 0 30px 10px;
}

.dottedBorder {
  width: 100%;
  max-width: 480px;
  height: 2px;
  background-image: radial-gradient(circle, var(--color-black) 1px, transparent 1px);
  background-size: 8px 2px;
  background-repeat: repeat-x;
  background-position: center;
}

.sectionHeader {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
}

.sectionTitle {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin: 0;
  width: 100%;
}

.titleLine1 {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.5;
  color: var(--color-black);
  padding-left: 10px;
}

.titleLine2 {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.4;
  color: var(--color-black);
  padding-left: 10px;
}

/*  */
.supportSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  padding: 20px 0;
  background: rgba(136, 211, 188, 0.15);
  border-radius: 0;
}

.titleLine2Support {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.5;
  color: var(--color-black);
  padding-left: 10px;
}

.titleLine1Support {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.4;
  padding-left: 10px;
  color: var(--color-black);
}

.tweetCardArea {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 10px 0 10px 10px;
  gap: 15px;
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.tweetCardArea::-webkit-scrollbar {
  display: none;
}

.moreButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 20px;
  gap: 8px;
  width: auto;
  max-width: 360px;
  height: auto;
  background: var(--color-white);
  border: 1px solid var(--color-black);
  border-radius: 8px;
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: var(--color-black);
  cursor: pointer;
  transition: background-color 0.2s, transform 0.1s;
}

.moreButton:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.moreButton:active {
  transform: scale(0.98);
}

.moreButton2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 20px;
  gap: 8px;
  width: auto;
  max-width: 360px;
  height: auto;
  background: #FFE6D5;
  border: 1px solid var(--color-black);
  border-radius: 8px;
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: var(--color-black);
  cursor: pointer;
  transition: background-color 0.2s, transform 0.1s;
}

.moreButton2:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.moreButton2:active {
  transform: scale(0.98);
}

.playIcon {
  width: 19.2px;
  height: 19.2px;
  object-fit: contain;
}

/*  */
.listButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 20px;
  gap: 8px;
  width: auto;
  max-width: 360px;
  height: auto;
  background: var(--color-white);
  border: 1px solid var(--color-black);
  border-radius: 8px;
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: var(--color-black);
  cursor: pointer;
  transition: background-color 0.2s, transform 0.1s;
  margin: 20px 0;
}

.listButton:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.listButton:active {
  transform: scale(0.98);
}

/*  */
.supportTypeSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  padding: 30px 0;
}

.supportTypeTitle {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.5;
  text-align: left;
  color: var(--color-black);
  margin: 0;
  width: 100%;
  padding-left: 10px;
}

.supportTypeDescription {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.8;
  text-align: left;
  color: var(--color-black);
  margin: 0;
  width: 100%;
}

/* FlexiCard */
.flexiCardArea {
  display: flex;
  flex-direction: row;
  gap: 15px;
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 10px 0 10px 10px;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.flexiCardArea::-webkit-scrollbar {
  display: none;
}

/* 2 */
.supportSection2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  padding: 30px 0;
  background: rgba(136, 211, 188, 0.15);
  border-radius: 0;
}

.colorBar {
  position: absolute;
  width: 55.96%;
  height: 13.78%;
  border-radius: 50%;
  box-shadow: 1px 1px 3px rgba(0,0,0,0.2);
}

.colorBar:nth-child(1) {
  /* 1 */
  left: 0%;
  right: 44.08%;
  top: 2.17%;
  bottom: 84.04%;
}

.colorBar:nth-child(2) {
  /* 2 */
  left: 44.03%;
  right: 0.04%;
  top: 18.46%;
  bottom: 67.76%;
}

.colorBar:nth-child(3) {
  left: 0%;
  right: 44.08%;
  top: 34.74%;
  bottom: 51.47%;
}

.colorBar:nth-child(4) {
