/*  -  */
.hamburgerMenu {
  display: none; /*  */
  position: fixed;
  top: 33px;
  left: 419px;
  z-index: 101;
  width: 100px;
  height: 44px;
  flex-shrink: 0;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.9);
  border: 1.5px solid #000000;
  border-radius: 5px;
  cursor: pointer;
  align-items: center;
  justify-content: space-between;
  padding: 0 9px;
  backdrop-filter: blur(2px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* 982px */
@media (max-width: 982px) {
  .hamburgerMenu {
    display: flex;
    top: 20px;
    right: 20px; /*  */
    left: auto; /* left */
  }
}

/* MEMU */
.menuText {
  font-family: 'DNP ShueiGoKinStd', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 0px;
  text-align: center;
  color: #000000;
  flex-grow: 1;
}

/*  - figma.txt Group 19 */
.menuIconContainer {
  position: relative;
  width: 19px;
  height: 11.62px;
}

/*  - figma.txt Rectangle 13,14,15 */
.menuIcon {
  position: absolute;
  width: 19px;
  height: 1.29px;
  background: #000000;
  border-radius: 0;
  margin: 0;
}

.menuIcon:nth-child(1) {
  top: 0px;         /* Rectangle 13 */
}

.menuIcon:nth-child(2) {
  top: 5.16px;      /* Rectangle 14 */
}

.menuIcon:nth-child(3) {
  top: 10.32px;     /* Rectangle 15 */
}

/* Close X  -  */
.closeIconText {
  font-family: Arial, sans-serif;
  font-size: 16px;
  font-weight: bold;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 19px;
  height: 11.62px;
  line-height: 1;
}

/*  */
.navigation {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 32px 23px; /* 32px 30px  32px 23px  */
  gap: 30px; /* 20px  30px  */
  isolation: isolate;
  position: fixed;
  top: 0;
  right: -270px; /*  */
  height: 100vh;
  transition: right 0.3s ease;
  z-index: 103;
  width: 270px; /* 270px */
  min-width: 270px; /* 270px */
  overflow-y: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
  padding-top: 68px; /* 84px  60px Main.module 74px50px24px */
  background-color: #FFFFFF;
  background: var(--color-white);
  border: 2px solid var(--color-black);
  border-radius: var(--radius-l);
  min-height: 670px;
}

/* Chrome, Safari, Opera */
.navigation::-webkit-scrollbar {
  display: none;
}

.navigationActive {
  right: 0; /*  -  */
  box-shadow: -2px 0 10px rgba(0, 0, 0, 0.2); /*  */
}

/*  -  */
.navItemsContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  margin-top: 54px; /* 90px  66px Main.module 74px50px24px */
}

/*  - DOM */
.verticalLine {
  position: absolute;
  width: 17px;
  left: -7px; /* padding-left: 30px */
  top: 0px; /* 0  24px Main.module 74px50px24px */
  background-image: url('../../assets/images/vector.png');
  background-size: 17px 100%;
  background-repeat: repeat-y;
  background-position: top;
  z-index: 8;
}

/* ::before */
.navigation::before {
  content: none;
}

