/* Section 02  */
.section02 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  gap: 30px;
  position: relative;
  width: 100%;
  max-width: 540px;
  margin: 0 auto; 
  background: rgba(163, 208, 250, 0.25); /* Section02 */
  border-radius: 0; /*  */
}

/*  */
.titleWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  gap: 8px; 
  width: 100%;
  max-width: 500px;
}

.logoContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 98px;
  height: 30px;
  margin-bottom: 0px;
}

.roadText {
  font-family: var(--font-primary);
  font-size: 16.5px;
  font-weight: bold;
  color: var(--color-black);
  margin-right: 2px;
  display: flex;
  align-items: center;
  position: relative;
  top: 10px;
}

.logoChar {
  width: auto;
  height: 66px; /* 33px66px */
  max-height: 66px; /* 33px66px */
  margin: 0 1px;
  object-fit: contain;
  position: relative;
  top: 5px;
}

.mainTitle {
  width: 100%;
  max-width: 394px;
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 32px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  color: var(--color-black);
  margin-top: 0px;
  margin-bottom: 0px;
}

.dotline {
  width: 100%;
  max-width: 364px;
  height: 10px;
  background-size: contain;
  background-repeat: repeat-x;
}

/*  */
/* .contentFrame - ContentFrame.module.css */

/* contentListSection02 */
.contentList {
  width: 100%;
  max-width: 460px;
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 0.1em;
  color: var(--color-black);
  text-align: left;
  padding-left: 10px;
  box-sizing: border-box;
}

.contentList p {
  margin: 0;
  padding: 0;
  line-height: 24px;
}

.projectButton {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px 10px;
  gap: 10px;
  width: 100%;
  max-width: 320px;
  height: auto;
  min-height: 46px;
  background: #FFF9CC; /*  */
  border: 1px solid var(--color-black);
  border-radius: 5px;
  cursor: pointer;
}

.playIcon {
  width: 19.2px;
  height: 19.2px;
  object-fit: contain;
}

.projectButton span {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: var(--color-black);
  flex-grow: 1;
  text-align: center;
}

/*  */
.experienceSection {
  width: 100%;
  max-width: 500px;
  margin-top: 20px;
}

/* Section02ExperienceSection */
.experience02 :global(.titleLine2) {
  font-size: 18px !important;
}

.experienceHeader {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 2px 0px;
  gap: 5px; /* 8px5px */
  margin-bottom: 15px;
  width: 100%;
}

.experienceTag {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px; /* 15px14px */
  color: var(--color-black);
  margin-bottom: -2px; /*  */
}

.experienceTitle {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px; /* 32px24px */
  margin-top: -2px; /*  */
  color: var(--color-black);
}

.tweetArea {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap; /* wrapnowrap */
  gap: 20px;
  width: 100%;
  justify-content: flex-start; /*  */
  overflow-x: auto; /*  */
  margin-bottom: 20px;
  -webkit-overflow-scrolling: touch; /*  */
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IEEdge */
}

/* Chrome, Safari, Opera */
.tweetArea::-webkit-scrollbar {
  display: none;
}

/*  TweetCard.module.css  */

.moreButton {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px 16px;
  gap: 10px;
  width: auto;
  min-width: 236px;
  height: 40px;
  background: var(--color-white);
  border: 1px solid var(--color-black);
  border-radius: 5px;
  margin: 20px auto 0;
  cursor: pointer;
}

.moreButton span {
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: var(--color-black);
  white-space: nowrap;
}

/*  */
@media (max-width: 768px) {
  .section02 {
    padding: 30px 15px;
  }
  
  .tweetArea {
    flex-direction: row; /*  */
    flex-wrap: nowrap; /*  */
    align-items: stretch; /*  */
    justify-content: flex-start; /*  */
  }
  /*  TweetCard.module.css  */
}
