/* src/components/MainContent/SectionCommon.module.css */
/* 00, 01, 02  */

/* (01 .titleContainer 
   00 .longDescription ) */

.titleContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0px 0px 0px 20px;
  width: 100%;
  max-width: 490px;
  height: 48px;
  position: relative;
  isolation: isolate;
  
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0;
  z-index: 0;
}

/*  */
.roadNumberContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  flex: none;
  order: 0;
  flex-grow: 0;
}

/*   
  .colorNavi  .colorBar 
  
  
  CSS
*/


/*  */
.roadNumber {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 3px;
  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0;
  z-index: 0;
  font-size: 0;
  width: 68px;
  height: 48px;
  position: relative;
}

.roadNumberImage {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/*  */
.mainTitle {
  width: 400px;
  height: 48px;
  font-family: 'Zen Kaku Gothic New', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 25px;
  line-height: 36px;
  display: flex;
  align-items: center;
  
  color: #000000;
  
  flex: none;
  order: 1;
  flex-grow: 0;
  margin: 0;
}

/*  */
.subtitle {
  width: 480px;
  height: auto; /*  */
  min-height: 23px; 
  
  font-family: 'Zen Kaku Gothic New';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 23px;
  text-align: center;
  
  color: #000000;
  
  flex: none;
  order: 1;
  flex-grow: 0;
  z-index: 1;
  margin: 0;
}

/*  */
.sectionTitle {
  width: 480px;
  height: auto; /*  */
  min-height: 23px;
  
  font-family: 'Zen Kaku Gothic New';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 23px;
  text-align: center;
  
  color: #000000;
  
  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0;
}

/*  */
.sectionDescription {
  width: 480px;
  height: auto; /*  */
  min-height: 25px;
  
  font-family: 'Zen Kaku Gothic New';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  letter-spacing: -0.05em;
  
  color: #000000;
  
  flex: none;
  order: 1;
  flex-grow: 0;
  margin: 0;
}

/*  (00 .longDescription ) */
.description,
.longDescription {
  width: 480px;
  height: auto; /*  */
  min-height: 25px;
  
  font-family: 'Zen Kaku Gothic New';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  letter-spacing: -0.05em;
  
  color: #000000;
  
  flex: none;
  order: 2;
  flex-grow: 0;
  z-index: 2;
  margin: 0;
}


/*  */
.quoteBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  gap: 10px;
  
  width: 480px;
  height: auto; /*  */
  min-height: 80px;
  
  border: 1px dashed #000000;
  border-radius: 10px;
  
  flex: none;
  order: 3;
  align-self: stretch;
  flex-grow: 0;
  z-index: 3;
}

.quoteText {
  width: 460px;
  height: auto; /*  */
  min-height: 60px;
  
  font-family: 'Zen Kaku Gothic New';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: -0.05em;
  
  color: #000000;
  
  flex: none;
  order: 0;
  flex-grow: 1;
  margin: 0;
}

/*  */
.supportBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  gap: 10px;
  
  width: 480px;
  min-height: 115px;
  
  border: 1px dashed #000000;
  border-radius: 10px;
  
  flex: none;
  align-self: stretch;
  flex-grow: 0;
}

.supportTitle {
  width: 460px;
  height: 25px;
  
  font-family: 'Zen Kaku Gothic New';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 25px;
  letter-spacing: -0.05em;
  
  color: #000000;
  
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0;
  margin: 0;
}

.supportDescription {
  width: 460px;
  min-height: 40px;
  
  font-family: 'Zen Kaku Gothic New';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.05em;
  
  color: #000000;
  
  flex: none;
  order: 1;
  align-self: stretch;
  flex-grow: 0;
  margin: 0;
}

/*  */
.projectButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 5px 10px;
  gap: 10px;
  
  max-width: 280px;
  height: 40px;
  
  background: rgba(163, 208, 250, 0.46);
  border-radius: 5px;
  border: none;
  cursor: pointer;
  
  flex: none;
  order: 2;
  flex-grow: 0;
}

.projectButtonHighlight {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 5px 10px;
  gap: 10px;
  
  width: 166px;
  max-width: 280px;
  height: 40px;
  
  background: #A3D0FA;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  
  flex: none;
  order: 5;
  flex-grow: 0;
}

.buttonIcon {
  width: 32px;
  height: 30px;
  
  flex: none;
  order: 0;
  flex-grow: 0;
}

.projectButton span,
.projectButtonHighlight span {
  font-family: 'Zen Kaku Gothic New';
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.05em;
  
  color: #000000;
  
  flex: none;
  order: 1;
  flex-grow: 1;
}

/*  */
.encouragementText {
  width: 480px;
  height: 46px;
  
  font-family: 'Zen Kaku Gothic New';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 23px;
  text-align: center;
  
  color: #000000;
  
  flex: none;
  order: 4;
  flex-grow: 0;
  z-index: 4;
  margin: 0;
}


/*  */
@media (max-width: 768px) {
  /*
    .sectionXXTop, .sectionXXMiddle 
    CSS
  */
  
  .titleContainer {
    width: 100%;
    max-width: 480px;
  }
  
  .subtitle,
  .description,
  .quoteBox,
  .encouragementText,
  .sectionTitle,
  .sectionDescription,
  .supportBox,
  .longDescription { 
    width: 100%;
    max-width: 480px;
  }
}