/* src/components/MainContent/commonPageLayout.module.css */
/* 
  
  
*/

.pageContainer {
  /* 1.  */
  width: 540px;
  min-height: 100vh;
  background: var(--color-white);
  border-radius: var(--radius-m);
  position: relative;
  padding-bottom: 0;

  /* 2. Layout.js  flex */
  flex: none;
  order: 1;
  flex-grow: 0;
  z-index: 1;
  
  /* 3.  (983px) */
  margin-left: 392px; /* 50 + 270 + 72 = 392px */
  margin-right: 0;
}

/* 4.  (982px) */
@media (max-width: 982px) {
  .pageContainer {
    width: 540px;
    margin: 0 auto; /*  */
  }
}

/* 5.  (560px) */
@media (max-width: 560px) {
  .pageContainer {
    width: 96.43%; /* 540/560 */
    max-width: 540px;
    margin: 0 1.79%; /* 1.79% */
    border-radius: 0;
    min-height: 100vh;
  }
}